<template>
  <div class="acount">
    <div class="topBgcolor"></div>

    <!-- 导航条 -->
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      left-arrow
      :fixed='true'
      @click-left="back"
    />

    <!-- 头部背景图 -->
    <div class="my-bg">
      <div class="bg auto-img">
      </div>
    </div>

    <div class="list-box">
      <van-cell title="个人头像 ：">
        <div class="avatar fr">
          <img
            :src="userInfo.userImg"
            alt=""
            class="auto-img avatar-img"
          >
          <van-uploader
            class="uploader-box"
            :after-read="uploadUserImg"
          />
        </div>
      </van-cell>
      <van-cell title="用户 ID ：">{{userInfo.userId}}</van-cell>
      <van-cell title="昵称 ：">
        <input
          type="text"
          class="nickname"
          v-model="userInfo.nickName"
          @change="modifyUserInfo('nickName','/updateNickName','C001')"
        >
      </van-cell>
      <van-cell title="手机号码 ：">{{userInfo.phone}}</van-cell>

      <!-- 简介-->
      <div class="desc-box">
        <div class="desc-title">简介 ：</div>
        <div>
          <textarea
            v-model="userInfo.desc"
            class="text-area"
            placeholder="这家伙太懒了，什么都没留下~"
            @change="modifyUserInfo('desc','/updateDesc','D001')"
          >
          </textarea>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import "../assets/less/account.css";
import { utils } from "../assets/js/utils";

export default {
  data() {
    return {
      userInfo: {},
      maxSize: 1 * 1024 * 1024,
    };
  },

  created() {
    this.getUserInfo();
  },

  methods: {
    // 跳转上一步
    back() {
      this.$router.go(-1);
    },

    // 获取个人资料
    getUserInfo() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == "B001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 上传头像
    uploadUserImg(file) {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      if (imgTypes.indexOf(type) === -1) {
        this.notify({
          message: "图片类型支支持" + imgTypes.join("."),
          color: "#fff",
          background: "#0c34ba",
        });
        return;
      }

      // 获取图片大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#0C34BA",
        });
        return;
      }

      // 

      // 获取base64码
      //   replace 替换一个与正则表达式匹配的子串
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    // 修改用户信息公用方法    昵称和简介
    modifyUserInfo(key, url, val) {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        [key]: this.userInfo[key],
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url,
        // 如果是get请求，参数需放在params,如果是post请求，参数需放在date
        data,
      })
        .then((result) => {
          this.$toast.clear();
          

          if (result.data.code != val) {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>
